@import "/var/www/dev.impl.ru/frontend/web/src/sass/components/_doctor_page.scss";
@import "/var/www/dev.impl.ru/frontend/web/src/sass/components/_footer.scss";
@import "/var/www/dev.impl.ru/frontend/web/src/sass/components/_header.scss";
@import "/var/www/dev.impl.ru/frontend/web/src/sass/components/_index.scss";
@import "/var/www/dev.impl.ru/frontend/web/src/sass/components/_med_specialties.scss";
@import "/var/www/dev.impl.ru/frontend/web/src/sass/components/_pagination.scss";
@import "/var/www/dev.impl.ru/frontend/web/src/sass/components/_recall_form.scss";
@import "/var/www/dev.impl.ru/frontend/web/src/sass/components/_reviews_block.scss";
